import React from 'react'
import './ProductCategory.css'
    function ProductCategory() {
        return (
            <section className="productcategory" style={{marginTop:"15px"}}>
            <div className="container">
                <div className="row">
                <div className="col-lg-4 col-md-6 col-sm-6">
                    <div className="sub__item">
                        <img src={process.env.PUBLIC_URL+"/Assets/img/icon/quality.png"} alt=""/>
                        <h5>Best Quality</h5>
                    
                        <span>Our metering pumps has solved chemical corrosion and leakage problem by precise design</span>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6">
                    <div className="sub__item">
                        <img src={process.env.PUBLIC_URL+"/Assets/img/icon/catalogue.png"} alt=""/>
                        <h5>Catalogue</h5>
                        <span>Get our newest product<span>We always up to date our catalogue product to make sure you have latest info</span></span>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6">
                    <div className="sub__item">
                        <img src={process.env.PUBLIC_URL+"/Assets/img/icon/office.png"} alt=""/>
                        <h5>Find Us</h5>
                        <span>We are always interested in seeking dynamic and committed distributors with the expertise, energy and resource to invest in</span>
                    </div>
                </div>
                
                    
                    
                </div>
            </div>
            </section>
        )
    }

export default ProductCategory
