import React from 'react'
import {Link} from 'react-router-dom'
function Head() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-lg-2 order-lg-1">
                        <div className="header__logo">
                            <Link to="/"><img  src={process.env.PUBLIC_URL+"/Assets/img/hansonlogo.png"} alt="Hanson Prima Engineering"/></Link>
                        </div>
                </div>
                <div className="col-lg-8 order-lg-1">
                        <div className="header__nav">
                            <h2>CV.HANSON PRIMA ENGINEERING</h2>
                        </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-10">
                    <div className="header__menu__option">
                        <nav className="header__menu">
                            <ul>
                                <li className="active"><Link to="/">Home</Link></li>
                                <li><Link to="/about-us">About</Link>
                        
                                </li>
                               
                                <li><a href="/product">Products</a>
                                    <ul className="dropdown">
                                        <li><a href="/product/kewpump">Kewpump</a></li>
                                        <li><a href="#s">Tapis Teknik</a></li>
                                        <li><a href="#s">Radicon Benzlers</a></li>
                                        <li><a href="#s">Challenge</a></li>
                                    </ul>
                                </li>
                                
                                <li><a href="/download">Downloads</a>
                                   
                                </li>

                            </ul>
                        </nav>
                        <div className="header__btn">
                            <a href="/contact" className="primary-btn">Contact Us</a>
                        </div>
                    </div>
                </div>
            </div>
            
            <div className="canvas__open">
                <i className="fa fa-bars"></i>
            </div>
            
        </div>
    )
}

export default Head
