import React from 'react'
import ProductCategory from './ProductCategory'
import {BrowserRouter as Router , Route,Switch,useRouteMatch} from 'react-router-dom'
import './Products.css'
import ProductSubcategorys from './KewpumpCategorys'
import ProductDetail from './ProductDetail'
import './ProductDetail.css'
import ItemKSSR from '../Content/ProductItem/ItemKSSR'
import itemKSDP from '../Content/ProductItem/itemKSDP'
import ItemKSSE2detail from '../Content/ProductItem/itemKSSE2detail.js'
import itemKSTO from '../Content/ProductItem/itemKSTO'
import CheonseiCategory from './CheonseiCategory'

function Products() {
    let {path} = useRouteMatch()
    return (
        <section className="productcategory">
            <Router>
            <div className="container">
                <div className="row" style={{marginBottom:"20px"}}>
                    <Switch>
                    <Route exact path={`${path}/`} component={ProductCategory}/>
                    <Route path={`${path}/kewpump/ks-se03`} component={ProductDetail}/>
                    <Route path={`${path}/kewpump/ks-se02`} component={ItemKSSE2detail}/>
                    <Route path={`${path}/kewpump/ks-sr`} component={ItemKSSR}/>
                    <Route path={`${path}/kewpump/ks-to`} component={itemKSTO}/>
                    <Route path={`${path}/kewpump/ks-kdp`} component={itemKSDP}/>
                    <Route path={`${path}/cheonsei`} component={CheonseiCategory}/>
                    <Route path={`${path}/kewpump`} component={ProductSubcategorys}/>
                   
                    
                    </Switch>
                    <div className="col-lg-3 order-lg-1">
                        <div className="services__appoinment">
                            <div className="services__title" style={{ marginBottom: '20px' }}>
                                <h5><img src="img/icons/services-icon.png" alt="" /> Download Our Catalog</h5>
                            </div>
                            <form action="#">
                                <input type="text" placeholder="Name" />
                                <input type="text" placeholder="Email" />
                                <input type="text" placeholder="Phone Number/Whatsapp"/>
                                <button type="submit" className="site-btn">Download</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            </Router>
        </section>
    )
}

export default Products
