import React from 'react'

function Banner() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                <section className="banner-side">
            <img alt="banner-home" style={{height:'400px',width:'100%'}}src={process.env.PUBLIC_URL+"/Assets/img/werehouse.jpg"}/>
        </section>
                </div>
            </div>
        </div>
        
    )
}

export default Banner
